import React from 'react';
import RehypeReact from 'rehype-react';
import styled from 'styled-components';
import { colors } from '../../data/color';
import HandlerForA from './HandlerForA';
/* Mapping the components to the markdown output */
const mobileWidth = '680px';
const fontColorNormal = colors.black1;
const fontColorHeadline = colors.black1;
const hLeft = colors.hLeft;
const mixinHeadline = `
  font-weight: bold;
  color: ${fontColorHeadline};
`;
const mixinList = `
  list-style-position: outside;
  background-color: ${colors.grayLight};
`;
const H1 = styled.h1 `
  ${mixinHeadline}
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 25px;

  @media screen and (max-width: ${mobileWidth}) {
    font-size: 18px;
  }
`;
const H2 = styled.h2 `
  ${mixinHeadline}
  font-size: 25px;
  margin-top: 60px;
  padding: 10px 40px;
  background: linear-gradient(
    170deg,
    ${colors.vivid1} 0%,
    ${colors.vivid2} 100%
  );
  color: ${colors.white1};
  @media screen and (max-width: ${mobileWidth}) {
    padding: 10px 20px;
  }
`;
const H3 = styled.h3 `
  ${mixinHeadline}

  font-size: 20px;

  padding: 5px 0 5px 15px;
  border-left: 5px solid ${hLeft};

  margin-top: 60px;
  margin-bottom: 20px;
  white-space: normal;
`;
const HR = styled.hr `
  margin-bottom: 30px;
`;
const B = styled.b `
  ${mixinHeadline}
  background: linear-gradient(transparent 50%, ${colors.fontMark1} 0%);
  color: ${colors.base1}
`;
const P = styled.p `
  line-height: 2;
  margin-top: 0;
  margin-bottom: 30px;
  color: ${fontColorNormal};

  font-size: 18px;
  @media screen and (max-width: ${mobileWidth}) {
    font-size: 16px;
  }
`;
const Blockquote = styled.blockquote `
  font-size: 17px;
  line-height: 36px;
  color: ${fontColorNormal};
  border-left: 0.3em solid lightgray;
  margin-left: 23px;
  padding: 0 10px;
  font-style: italic;
`;
const SHOULD_NOT_USE_TAG = styled.span `
  color: red;
`;
const Table = styled.table `
  width: 100%;
  max-width: 100%;
  margin-bottom: 20px;
  border-collapse: collapse;
  color: ${fontColorNormal};
`;
const Ul = styled.ul `
  ${mixinList}

  margin-bottom: 40px;
`;
const Ol = styled.ol `
  ${mixinList}
  margin-bottom: 40px;
`;
const Li = styled.li `
  ${mixinList}
  padding: 10px
`;
const Td = styled.td `
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
`;
const Tr = styled.tr `
  border-bottom: 1px solid ${colors.grayDark};
`;
/**
 * Markdown ReactComponent
 *
 * - RemarkParserMarkdown >> html
 * - Compier: html >> react
 *
 * ## Compiler
 * - CSS: CSSCompilerCompilerscss  import 
 * - AMP: img  am-img GatsbyJSAMP
 */
export const renderReact = new RehypeReact({
    createElement: React.createElement,
    components: {
        h1: H1,
        h2: H2,
        h3: H3,
        h4: SHOULD_NOT_USE_TAG,
        h5: SHOULD_NOT_USE_TAG,
        h6: SHOULD_NOT_USE_TAG,
        hr: HR,
        a: HandlerForA,
        b: B,
        strong: B,
        p: P,
        blockquote: Blockquote,
        table: Table,
        td: Td,
        tr: Tr,
        ul: Ul,
        ol: Ol,
        li: Li,
    },
}).Compiler;
export default renderReact;
