import React from 'react';
import { Link } from 'gatsby';
import styled from 'styled-components';
import Button from '@material-ui/core/Button';
export const SubPageLinks = ({ context }) => {
    return (<Container>
      <CategoryContainer>
        <div></div>
        {context.categories.map(({ name, count, path }) => (<Button key={name}>
            <Link to={path}>{`${name}(${count})`}</Link>
          </Button>))}
      </CategoryContainer>

      <div>
        <div></div>
        {context.tags.map(({ name, count, path }) => (<Button key={name}>
            <Link to={path}>
              <div>{`${name}(${count})`}</div>
            </Link>
          </Button>))}
      </div>
    </Container>);
};
const Container = styled.div `
  padding-top: 50px;
`;
const CategoryContainer = styled.div `
  padding-bottom: 20px;
`;
