import React from 'react';
import { HeaderTitle } from '../../components/atoms';
// material-ui/core
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Slide from '@material-ui/core/Slide';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import { makeStyles, createStyles } from '@material-ui/core/styles';
// Misc
import styled from 'styled-components';
import { colors } from '../../../data/color';
const useStyles = makeStyles((theme) => createStyles({
    root: {
        display: 'flex',
    },
    appBar: {
        backgroundImage: `linear-gradient(170deg, ${colors.vivid1} ,  ${colors.vivid2} 70%, ${colors.vivid3})`,
    },
    // toolbar: theme.mixins.toolbar,
    content: {
        width: '100vw',
    },
}));
const HideOnScroll = ({ children, window, }) => {
    // readme: https://material-ui.com/components/app-bar/#hide-app-bar
    const trigger = useScrollTrigger({ target: window ? window() : undefined });
    return (<Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>);
};
export const Navigation = (props) => {
    const { children } = props;
    const classes = useStyles();
    return (<>
      <div className={classes['root']}>
        
        <HideOnScroll {...props}>
          <AppBar position="fixed" className={classes['appBar']}>
            <Toolbar>
              <Typography variant="h6" noWrap>
                <HeaderTitle />
              </Typography>
            </Toolbar>
          </AppBar>
        </HideOnScroll>

        
        <main className={classes['content']}>
          <div className={classes['toolbar']}>
            <Container>{children}</Container>
          </div>
        </main>
      </div>
    </>);
};
// TODO: https://codepen.io/P1N2O/pen/pyBNzX 
const Container = styled.div `
  display: flex;
  min-height: 100%;
  flex-direction: column;
  flex: 1;
`;
